body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu{
    border-radius: 0;
}

.ui.menu {
    border-radius: 0 !important;
}

.pageWrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.searchBar{
    margin-bottom: 5%;
    width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

i{
    padding: 2px;
}

.searchInput{
    width: 80%;
    /*padding: 10px;*/
    border-radius: 5%;
}

.productList{
    line-height: 0;
    -webkit-column-count: 5;
    -webkit-column-gap:   0;
    -moz-column-count:    5;
    -moz-column-gap:      0;
    column-count:         5;
    grid-column-gap:           0;
    column-gap:           0;
}
.productList img{
    /*width: 100%;*/
    /*height: auto;*/
    object-fit: contain;
    height: auto;
    max-height: 60vmin;
    width: 100%;
}

.product{
    margin: 20px;
    opacity: 1;
}

.product:hover{
    cursor: pointer;
    opacity: .9;
}

@media (max-width: 1200px) {
    .productList {
        -webkit-column-count: 4;
        column-count:         4;
    }
}
@media (max-width: 1000px) {
    .productList {
        -webkit-column-count: 3;
        column-count:         3;
    }
}
@media (max-width: 800px) {
    .productList {
        -webkit-column-count: 2;
        column-count:         2;
    }
}
@media (max-width: 400px) {
    .productList {
        -webkit-column-count: 1;
        column-count:         1;
    }
}

.noResults{

}
.productImg{
    /*padding: 2%;*/
    object-fit: contain;
    height: auto;
    max-height: 80vmin;
    width: 100%;
    max-width: 50vmax;
}

.page{
    /*padding: 2%;*/
    display: -webkit-flex;
    display: flex;
}

.right{
    width: 50%;
    padding: 3%;
}

.left{
    width: 50%;
    padding: 3%;
}

.cartButton{
    padding: 5px;
    margin-top: 10px;
    border-radius: 5px;
}

.cartButton:focus{
    outline: 0;
}

.title{
    font-size: 24px;
    font-weight: bold;
    margin: 0;
}

